import React, { useRef, useState } from "react";
import emailjs from "@emailjs/browser";
import { init } from "@emailjs/browser";
init(process.env.ID);
import swal from "sweetalert";

const asyncFunc = async () => {
  let promise = new Promise((resolve, reject) => {
    setTimeout(() => resolve("Message envoy!"), 0);
  });

  let result = await promise;

  swal(result);
};

const ContactForm = () => {
  const form = useRef();
  const [username, setUsername] = useState("");
  const [email, setEmail] = useState("");
  const [message, setGender] = useState("");
  const [disable, setDisable] = useState(0);

  const handleusername = (e) => {
    const getUsername = e.target.value;
    setUsername(getUsername);
  };

  const handleemail = (e) => {
    const getEmail = e.target.value;
    setEmail(getEmail);
  };

  const handlemessage = (e) => {
    const getMessage = e.target.value;
    setGender(getMessage);
  };

  const sendEmail = (e) => {
    e.preventDefault();
    console.log(`${username} / ${email} / ${message}`);
    setDisable(true);
    const formMess = document.querySelector(".formMessage");

    emailjs
      .sendForm(
        "service_xobc9bh",
        "template_n11ay77",
        form.current,
        process.env.REACT_APP_ID
      )
      .then(
        (res) => {
          console.log(res.text);
          form.current.reset();
          formMess.innerHTML = "<p className='success'>Message envoy !</p>";

          setTimeout(() => {
            formMess.innerHTML = "";
          }, 10000);
        },
        (err) => {
          console.log(err.text);
          formMess.innerHTML =
            "<p className='error'>Une erreur s'est produite, veuillez ressayer</p>";

          setTimeout(() => {
            formMess.innerHTML = "";
          }, 10000);
        }
      );
  };

  return (
    <div className="form-container">
      <h2>Contactez-moi</h2>
      <form ref={form} onSubmit={sendEmail} className="form-content">
        <label>Nom</label>
        <input
          type="text"
          name="name"
          required
          autoComplete="off"
          id="name"
          onChange={(e) => handleusername(e)}
        />
        <label>Email</label>
        <input
          type="email"
          name="email"
          required
          id="email"
          onChange={(e) => handleemail(e)}
        />
        <label>Message</label>
        <textarea
          name="message"
          required
          id="mess"
          onChange={(e) => handlemessage(e)}
        />
        <input
          type="submit"
          value="Envoyer"
          className="hover button"
          disabled={disable}
        />
      </form>
      <div className="formMessage"></div>
    </div>
  );
};

export default ContactForm;
